/* templates display */
.template-slide-left-start {
  -webkit-animation: slideleft 1s normal linear;
          animation: slideleft 1s normal linear;
  will-change: transform;
}
.template-slide-left-end {
  -webkit-transform: translateX(-100%);
      -ms-transform: translateX(-100%);
          transform: translateX(-100%);
}

@-webkit-keyframes slideleft {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  100% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
}

@keyframes slideleft {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  100% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
}

.template-slide-right-start {
  -webkit-animation: slideleft 1s reverse linear;
          animation: slideleft 1s reverse linear;
  will-change: transform;
}
.template-slide-right-end {
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
}



